'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { NavItem } from './sidebar-nav';
import {
    LogOut,
    Menu,
    X,
    Home,
    ClipboardList,
    Trophy,
    LayoutDashboard,
    Users,
    FileText,
    Settings,
    Layers,
    HeartPulse
} from 'lucide-react';
import { useState } from 'react';

const ICON_MAP: Record<string, any> = {
    Home,
    ClipboardList,
    Trophy,
    LayoutDashboard,
    Users,
    FileText,
    Settings,
    Layers,
    HeartPulse
};

interface SidebarProps {
    items: NavItem[];
    userName: string;
    userRole: string;
}

export function Sidebar({ items, userName, userRole }: SidebarProps) {
    const pathname = usePathname();
    const [isOpen, setIsOpen] = useState(false);

    const roleLabels: Record<string, string> = {
        STUDENT: 'Aluno',
        TEACHER: 'Professor',
        PSYCHOLOGIST: 'Psicólogo',
        COUNSELOR: 'Orientador',
        MANAGER: 'Gestor',
        ADMIN: 'Administrador',
    };

    const navContent = (
        <div className="flex flex-col h-full bg-slate-900 text-slate-300">
            <div className="p-6 border-b border-slate-800">
                <h2 className="text-xl font-bold text-white flex items-center gap-2">
                    <div className="h-8 w-8 bg-indigo-600 rounded-lg flex items-center justify-center">
                        <span className="text-white font-black text-sm">GS</span>
                    </div>
                    Socioemocional
                </h2>
            </div>

            <nav className="flex-1 p-4 space-y-1">
                {items.map((item) => {
                    const Icon = ICON_MAP[item.iconName] || Home;
                    const isActive = pathname === item.href || (item.href !== '/' && pathname.startsWith(item.href));

                    return (
                        <Link
                            key={item.href}
                            href={item.href}
                            onClick={() => setIsOpen(false)}
                            className={`flex items-center gap-3 px-3 py-2 rounded-lg transition-all ${isActive
                                ? 'bg-indigo-600 text-white shadow-lg shadow-indigo-900/20'
                                : 'hover:bg-slate-800 hover:text-white'
                                }`}
                        >
                            <Icon size={18} />
                            <span className="font-medium text-sm">{item.label}</span>
                        </Link>
                    );
                })}
            </nav>

            <div className="p-4 border-t border-slate-800 bg-slate-950/50">
                <div className="flex items-center gap-3 mb-4 px-2">
                    <div className="h-8 w-8 rounded-full bg-slate-700 flex items-center justify-center text-white font-bold text-xs">
                        {userName.charAt(0).toUpperCase()}
                    </div>
                    <div className="overflow-hidden">
                        <p className="text-sm font-semibold text-white truncate">{userName}</p>
                        <p className="text-xs text-slate-500 uppercase tracking-wider">{roleLabels[userRole] || userRole}</p>
                    </div>
                </div>

                <form action="/auth/signout" method="POST">
                    <button
                        type="submit"
                        className="w-full flex items-center gap-3 px-3 py-2 text-sm text-slate-400 hover:text-red-400 hover:bg-red-400/10 rounded-lg transition-all"
                    >
                        <LogOut size={16} />
                        <span>Encerrar Sessão</span>
                    </button>
                </form>
            </div>
        </div>
    );

    return (
        <>
            {/* Mobile Toggle */}
            <div className="lg:hidden fixed top-px left-0 right-0 z-50 flex items-center justify-between p-4 bg-slate-900 border-b border-slate-800">
                <h2 className="font-bold text-white text-sm">Socioemocional</h2>
                <button
                    onClick={() => setIsOpen(!isOpen)}
                    className="p-1 text-slate-300"
                >
                    {isOpen ? <X size={24} /> : <Menu size={24} />}
                </button>
            </div>

            {/* Sidebars */}
            <div className={`
        fixed inset-y-0 left-0 z-40 w-64 transform transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0
        ${isOpen ? 'translate-x-0' : '-translate-x-full'}
      `}>
                {navContent}
            </div>

            {/* Backdrop */}
            {isOpen && (
                <div
                    className="fixed inset-0 z-30 bg-slate-950/50 backdrop-blur-sm lg:hidden"
                    onClick={() => setIsOpen(false)}
                />
            )}
        </>
    );
}
